import * as THREE from "three";
// import fragment from "url:./shader/fragment.glsl";
// import vertex from "url:./shader/vertex.glsl";
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { GUI } from 'three/examples/jsm/libs/dat.gui.module';
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js';
import t from 'url:../images/flower-img-1.jpg';
import t1 from 'url:../images/flower-1-end.jpg';
import gsap from 'gsap';
import gsapCore from "gsap";
export default class Sketch {
  constructor(options) {
    this.scene = new THREE.Scene();

    this.container = options.dom;
    this.width = this.container.offsetWidth;
    this.height = this.container.offsetHeight;
    this.renderer = new THREE.WebGLRenderer();
    this.renderer.setPixelRatio(window.devicePixelRatio);
    this.renderer.setSize(this.width, this.height);
    this.renderer.setClearColor(0x000000, 1); 
    // this.renderer.outputEncoding = THREE.sRGBEncoding;

    this.container.appendChild(this.renderer.domElement);

    this.camera = new THREE.PerspectiveCamera(
      70,
      window.innerWidth / window.innerHeight,
      0.001,
      5000
    );

    // var frustumSize = 10;
    // var aspect = window.innerWidth / window.innerHeight;
    // this.camera = new THREE.OrthographicCamera( frustumSize * aspect / - 2, frustumSize * aspect / 2, frustumSize / 2, frustumSize / - 2, -1000, 1000 );
    this.camera.position.set(0, 0, 1500);
    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.time = 0;

    this.video = document.getElementById('video-1');
    this.video.addEventListener('ended', () => {
      gsap.to(this.video, {
        duration: 0.2,
        opacity: 0
      });

      gsap.to(this.material.uniforms.distortion, {
        duration: 2,
        value: 3,
        ease: 'power2.inOut'
      });

      gsap.to(this.material.uniforms.progress, {
        duration: 1,
        delay: 1.5,
        value: 1
      });

      gsap.to(this.bloomPass, {
        duration: 2,
        strength: 3,
        ease: 'power2.in'
      });

      gsap.to(this.material.uniforms.distortion, {
        duration: 2,
        value: 0,
        delay: 2,
        ease: 'power2.inOut'
      });

      gsap.to(this.bloomPass, {
        duration: 2,
        strength: 0,
        delay: 2,
        ease: 'power2.out',
        onComplete: () => {
          this.video.currentTime = 0;
          this.video.play();
          gsap.to(this.video, {
            duration: 0.2,
            opacity: 1
          });
        }
      });
    })
    this.isPlaying = true;
    
    this.addPostProcessing();

    this.addObjects();
    this.resize();
    this.render();
    this.setupResize();
    this.settings();
  }

  addPostProcessing() {
    this.renderScene = new RenderPass( this.scene, this.camera );
    this.bloomPass = new UnrealBloomPass( new THREE.Vector2( window.innerWidth, window.innerHeight ), 1.5, 0.4, 0.85 );
    this.bloomPass.threshold = this.settings.bloomThreshold;
    this.bloomPass.strength = this.settings.bloomStrength;
    this.bloomPass.radius = this.settings.bloomRadius;

    this.composer = new EffectComposer( this.renderer );
    this.composer.addPass( this.renderScene );
    this.composer.addPass( this.bloomPass );
  }
  settings() {
    let that = this;
    this.settings = {
      distortion: 0,
      bloomStrength: 0
    };
    this.gui = new GUI();
    this.gui.add(this.settings, "distortion", 0, 3, 0.01);
    this.gui.add(this.settings, "bloomStrength", 0, 10, 0.01);
  }

  setupResize() {
    window.addEventListener("resize", this.resize.bind(this));
  }

  resize() {
    this.width = this.container.offsetWidth;
    this.height = this.container.offsetHeight;
    this.renderer.setSize(this.width, this.height);
    this.camera.aspect = this.width / this.height;
    this.camera.updateProjectionMatrix();
    this.composer.setSize(this.width, this.height);
  }

  addObjects() {
    let that = this;
    this.material = new THREE.ShaderMaterial({
      extensions: {
        derivatives: "#extension GL_OES_standard_derivatives : enable"
      },
      side: THREE.DoubleSide,
      uniforms: {
        time: { type: "f", value: 0 },
        distortion: { type: "f", value: 0 },
        t:    { type: "t", value: new THREE.TextureLoader().load(t) },
        t1:   { type: "t", value: new THREE.TextureLoader().load(t1) },
        resolution: { type: "v4", value: new THREE.Vector4() },
        uvRate1: {
          value: new THREE.Vector2(1, 1)
        },
        progress: { type: "f", value: 0 }
      },
      // wireframe: true,
      // transparent: true,
      vertexShader: `uniform float time;
      varying vec2 vUv;
      varying vec3 vPosition;
      uniform sampler2D t;
      uniform float distortion;
      float PI = 3.141592653589793238;
      // Curl Noise by: https://gist.github.com/Makio64/56d15142d30f5add045ca1bb220e97ca
      vec4 permute(vec4 x){return mod(x*x*34.+x,289.);}
      float snoise(vec3 v){
        const vec2 C = 1./vec2(6,3);
        const vec4 D = vec4(0,.5,1,2);
        vec3 i  = floor(v + dot(v, C.yyy));
        vec3 x0 = v - i + dot(i, C.xxx);
        vec3 g = step(x0.yzx, x0.xyz);
        vec3 l = 1. - g;
        vec3 i1 = min( g.xyz, l.zxy );
        vec3 i2 = max( g.xyz, l.zxy );
        vec3 x1 = x0 - i1 + C.x;
        vec3 x2 = x0 - i2 + C.y;
        vec3 x3 = x0 - D.yyy;
        i = mod(i,289.);
        vec4 p = permute( permute( permute(
          i.z + vec4(0., i1.z, i2.z, 1.))
        + i.y + vec4(0., i1.y, i2.y, 1.))
        + i.x + vec4(0., i1.x, i2.x, 1.));
        vec3 ns = .142857142857 * D.wyz - D.xzx;
        vec4 j = p - 49. * floor(p * ns.z * ns.z);
        vec4 x_ = floor(j * ns.z);
        vec4 x = x_ * ns.x + ns.yyyy;
        vec4 y = floor(j - 7. * x_ ) *ns.x + ns.yyyy;
        vec4 h = 1. - abs(x) - abs(y);
        vec4 b0 = vec4( x.xy, y.xy );
        vec4 b1 = vec4( x.zw, y.zw );
        vec4 sh = -step(h, vec4(0));
        vec4 a0 = b0.xzyw + (floor(b0)*2.+ 1.).xzyw*sh.xxyy ;
        vec4 a1 = b1.xzyw + (floor(b1)*2.+ 1.).xzyw*sh.zzww ;
        vec3 p0 = vec3(a0.xy,h.x);
        vec3 p1 = vec3(a0.zw,h.y);
        vec3 p2 = vec3(a1.xy,h.z);
        vec3 p3 = vec3(a1.zw,h.w);
        vec4 norm = inversesqrt(vec4(dot(p0,p0), dot(p1,p1), dot(p2, p2), dot(p3,p3)));
        p0 *= norm.x;
        p1 *= norm.y;
        p2 *= norm.z;
        p3 *= norm.w;
        vec4 m = max(.6 - vec4(dot(x0,x0), dot(x1,x1), dot(x2,x2), dot(x3,x3)), 0.);
        return .5 + 12. * dot( m * m * m, vec4( dot(p0,x0), dot(p1,x1),dot(p2,x2), dot(p3,x3) ) );
      }

      vec3 snoiseVec3( vec3 x ){
        return vec3(  snoise(vec3( x )*2.-1.),
                      snoise(vec3( x.y - 19.1 , x.z + 33.4 , x.x + 47.2 ))*2.-1.,
                      snoise(vec3( x.z + 74.2 , x.x - 124.5 , x.y + 99.4 )*2.-1.)
        );
      }

      vec3 curlNoise( vec3 p ){
        const float e = .1;
        vec3 dx = vec3( e   , 0.0 , 0.0 );
        vec3 dy = vec3( 0.0 , e   , 0.0 );
        vec3 dz = vec3( 0.0 , 0.0 , e   );

        vec3 p_x0 = snoiseVec3( p - dx );
        vec3 p_x1 = snoiseVec3( p + dx );
        vec3 p_y0 = snoiseVec3( p - dy );
        vec3 p_y1 = snoiseVec3( p + dy );
        vec3 p_z0 = snoiseVec3( p - dz );
        vec3 p_z1 = snoiseVec3( p + dz );

        float x = p_y1.z - p_y0.z - p_z1.y + p_z0.y;
        float y = p_z1.x - p_z0.x - p_x1.z + p_x0.z;
        float z = p_x1.y - p_x0.y - p_y1.x + p_y0.x;

        const float divisor = 1.0 / ( 2.0 * e );
        return normalize( vec3( x , y , z ) * divisor );
      }
      void main() {
        vUv = uv;
        // curlNoise returns a range between 0 and 1 we can use that and multiply it with the position to get them
        // moving around the screen
        vec3 distortion1 = vec3(position.x * 2., position.y, 1.) * curlNoise(
          vec3(position.x * 0.002 + time * 0.1, position.y * 0.008 + time * 0.1, 
          (position.x + position.y) * 0.02)) * distortion;
        vec3 finalPosition = position + distortion1;


        vec4 mvPosition = modelViewMatrix * vec4( finalPosition, 1. );
        gl_PointSize = 0.1;
        gl_Position = projectionMatrix * mvPosition;
      }`,
      fragmentShader: `uniform float time;
      uniform float progress;
      uniform sampler2D t;
      uniform sampler2D t1;
      uniform vec4 resolution;
      varying vec2 vUv;
      varying vec3 vPosition;
      float PI = 3.141592653589793238;
      void main()	{
        vec4 tt = texture2D(t, vUv);
        vec4 tt1 = texture2D(t1, vUv);
        vec4 finalTexture = mix(tt, tt1, 1.0);
        gl_FragColor = finalTexture;
        // Discard black particles
        if(gl_FragColor.r < 0.1 && gl_FragColor.b < 0.1 && gl_FragColor.g < 0.1) discard;
      }`
    });

    // We use 420x820 as that is the pixel dimensions of the initial video
    // This way we can map points to actual pixels
    // Texture vs draw them in Canvas2D 
    // and get the color of each pixel and then send them to the shader
    
    this.geometry = new THREE.PlaneBufferGeometry(480 * 1.77, 820 * 1.77, 420, 820);

    this.plane = new THREE.Points(this.geometry, this.material);
    this.scene.add(this.plane);
  }

  stop() {
    this.isPlaying = false;
  }

  play() {
    if(!this.isPlaying){
      this.render()
      this.isPlaying = true;
    }
  }

  render() {
    if (!this.isPlaying) return;
    this.time += 0.05;
    this.material.uniforms.time.value = this.time;
    // this.material.uniforms.distortion.value = this.settings.distortion;
    // this.bloomPass.strength = this.settings.bloomStrength;
    
    requestAnimationFrame(this.render.bind(this));
    // this.renderer.render(this.scene, this.camera);
    this.composer.render();
  }
}

new Sketch({
  dom: document.getElementById("container")
});
